import { compareExecutionPoints, isExecutionPointsGreaterThan, isExecutionPointsLessThan } from "bvaughn-architecture-demo/src/utils/time";
import { binarySearch } from "protocol/utils";
const nullTuple = [null, null];
export function findClosestHitPoint(hitPoints, executionPoint) {
  const index = binarySearch(0, hitPoints.length, index => compareExecutionPoints(executionPoint, hitPoints[index].point));

  if (index >= 0 && index < hitPoints.length) {
    const hitPoint = hitPoints[index];
    return [hitPoint, index];
  }

  return nullTuple;
}
export function findHitPoint(hitPoints, executionPoint) {
  const [hitPoint, hitPointIndex] = findClosestHitPoint(hitPoints, executionPoint);

  if (hitPoint !== null && hitPoint.point === executionPoint) {
    return [hitPoint, hitPointIndex];
  }

  return nullTuple;
}
export function findHitPointAfter(hitPoints, executionPoint) {
  const [hitPoint, index] = findClosestHitPoint(hitPoints, executionPoint);

  if (hitPoint !== null) {
    if (isExecutionPointsGreaterThan(hitPoint.point, executionPoint)) {
      return [hitPoint, index];
    } else {
      const nextIndex = index + 1;

      if (nextIndex < hitPoints.length) {
        return [hitPoints[nextIndex], nextIndex];
      }
    }
  }

  return nullTuple;
}
export function findHitPointBefore(hitPoints, executionPoint) {
  const [hitPoint, index] = findClosestHitPoint(hitPoints, executionPoint);

  if (hitPoint !== null) {
    if (isExecutionPointsLessThan(hitPoint.point, executionPoint)) {
      return [hitPoint, index];
    } else {
      const prevIndex = index - 1;

      if (prevIndex >= 0) {
        return [hitPoints[prevIndex], prevIndex];
      }
    }
  }

  return nullTuple;
}
export function findPointForLocation(points, sourceId, lineNumber) {
  return points.find(point => point.location.sourceId === sourceId && point.location.line === lineNumber) || null;
}