import { useEffect } from "react"; // Closes a modal dialog if the user clicks outside of it or types "Escape"

export default function useModalDismissSignal(modalRef, dismissCallback, dismissOnClickOutside = true) {
  useEffect(() => {
    const element = modalRef.current;

    if (element === null) {
      return;
    }

    const handleDocumentKeyDown = event => {
      if (event.key === "Escape") {
        dismissCallback();
      }
    };

    const handleDocumentClick = event => {
      if (!element.contains(event.target)) {
        event.stopPropagation();
        event.preventDefault();
        dismissCallback();
      }
    };

    let ownerDocument = null; // Delay until after the current call stack is empty,
    // in case this effect is being run while an event is currently bubbling.
    // In that case, we don't want to listen to the pre-existing event.

    let timeoutID = setTimeout(() => {
      timeoutID = null; // It's important to listen to the ownerDocument to support browser extensions.
      // The root document might belong to a different window.

      ownerDocument = element.ownerDocument;
      ownerDocument.addEventListener("keydown", handleDocumentKeyDown);

      if (dismissOnClickOutside) {
        ownerDocument.addEventListener("click", handleDocumentClick, true);
      }

      if (dismissOnClickOutside) {
        ownerDocument.addEventListener("scroll", dismissCallback, true);
      }
    }, 0);
    return () => {
      if (timeoutID !== null) {
        clearTimeout(timeoutID);
      }

      if (ownerDocument !== null) {
        ownerDocument.removeEventListener("keydown", handleDocumentKeyDown);
        ownerDocument.removeEventListener("click", handleDocumentClick, true);
      }
    };
  }, [modalRef, dismissCallback, dismissOnClickOutside]);
}