import jsTokens from "js-tokens";
import { createWakeable } from "../utils/suspense";
import { preCacheObjects } from "./ObjectPreviews";
import { STATUS_PENDING, STATUS_REJECTED, STATUS_RESOLVED } from "./types";

function getKey(focusRange, location, code, condition) {
  const rangeString = focusRange ? `${focusRange.begin}-${focusRange.end}` : "-";
  return `${rangeString}:${location.sourceId}:${location.line}:${location.column}:${code}:${condition || ""}`;
}

const locationAndTimeToValueMap = new Map(); // TODO (FE-469) Filter in-memory if the range gets smaller (and we haven't overflowed)
// Currently we re-run the analysis which seems wasteful.

export function runAnalysisSuspense(client, focusRange, location, code, condition) {
  const key = getKey(focusRange, location, code, condition);
  let record = locationAndTimeToValueMap.get(key);

  if (record == null) {
    const wakeable = createWakeable();
    record = {
      status: STATUS_PENDING,
      value: wakeable
    };
    locationAndTimeToValueMap.set(key, record);

    if (!condition && canRunLocalAnalysis(code)) {
      runLocalAnalysis(code, record, wakeable);
    } else {
      runRemoteAnalysis(client, focusRange, location, code, condition, record, wakeable);
    }
  }

  if (record.status === STATUS_RESOLVED) {
    return record.value;
  } else {
    throw record.value;
  }
}
export function canRunLocalAnalysis(code) {
  const tokens = jsTokens(code); // @ts-ignore

  for (let token of tokens) {
    switch (token.type) {
      case "IdentifierName":
        {
          switch (token.value) {
            case "false":
            case "Infinity":
            case "NaN":
            case "null":
            case "true":
            case "undefined":
              {
                // Supported
                break;
              }

            default:
              {
                return false;
              }
          }

          break;
        }

      case "Punctuator":
        {
          switch (token.value) {
            case ",":
              {
                // Supported
                break;
              }

            default:
              {
                return false;
              }
          }

          break;
        }

      case "NoSubstitutionTemplate":
      case "NumericLiteral":
      case "StringLiteral":
      case "WhiteSpace":
        {
          // supported
          break;
        }

      default:
        {
          return false;
        }
    }
  }

  return true;
}

async function runLocalAnalysis(code, record, wakeable) {
  try {
    const analysisResults = await new Promise((resolve, reject) => {
      if (code === "location") {
        reject();
      }

      const escapedCode = code.replace(/\\/g, "\\\\").replace(/"/g, '\\"'); // Most globals (like window and document) aren't defined in a Worker, but the location global is.

      const workerCode = `
          const values = (() => eval("[${escapedCode}]"))();
          postMessage(values);
      `;
      const blob = new Blob([workerCode], {
        type: "text/javascript"
      });
      const objectURL = URL.createObjectURL(blob);
      const worker = new Worker(objectURL);
      worker.addEventListener("message", event => {
        const values = event.data;

        const analysisResults = timeStampedPoint => ({
          executionPoint: timeStampedPoint.point,
          isRemote: false,
          pauseId: null,
          time: timeStampedPoint.time,
          values
        });

        resolve(analysisResults);
        clearTimeout(timeoutID);
      });
      worker.addEventListener("error", event => {
        reject(event.message);
        clearTimeout(timeoutID);
      });
      worker.postMessage("start");
      const timeoutID = setTimeout(() => {
        worker.terminate();
        reject("Timed out");
      }, 5000);
    });
    record.status = STATUS_RESOLVED;
    record.value = analysisResults;
    wakeable.resolve(analysisResults);
  } catch (error) {
    record.status = STATUS_REJECTED;
    record.value = error;
    wakeable.reject(error);
  }
}

async function runRemoteAnalysis(client, focusRange, location, code, condition, record, wakeable) {
  try {
    const results = await client.runAnalysis({
      effectful: false,
      location,
      mapper: createMapperForAnalysis(code, condition),
      range: focusRange || undefined
    });
    const resultsMap = new Map();
    results.forEach(result => {
      const objects = result.data.objects;

      if (objects) {
        // Pre-cache pause data so we don't need to refetch when rendering in inspecting values in the console.
        preCacheObjects(result.pauseId, objects);
      }

      resultsMap.set(result.point, {
        executionPoint: result.point,
        isRemote: true,
        pauseId: result.pauseId,
        time: result.time,
        values: result.values
      });
    });

    const analysisResults = timeStampedPoint => {
      return resultsMap.get(timeStampedPoint.point) || null;
    };

    record.status = STATUS_RESOLVED;
    record.value = analysisResults;
    wakeable.resolve(analysisResults);
  } catch (error) {
    record.status = STATUS_REJECTED;
    record.value = error;
    wakeable.reject(error);
  }
}

function createMapperForCondition(condition) {
  return `
    const { result: conditionResult } = sendCommand(
      "Pause.evaluateInFrame",
      { frameId, expression: ${JSON.stringify(condition)}, useOriginalScopes: true }
    );
    addPauseData(conditionResult.data);
    if (conditionResult.returned) {
      const { returned } = conditionResult;
      if ("value" in returned && !returned.value) {
        return [];
      }
      if (!Object.keys(returned).length) {
        // Undefined.
        return [];
      }
    }
  `;
}

function createMapperForAnalysis(code, condition) {
  const escapedCode = code.replace(/"/g, '\\"');
  return `
    const finalData = { frames: [], scopes: [], objects: [] };
    const { point, time, pauseId } = input;
    const { frameId, functionName, location } = getTopFrame();

    ${condition ? createMapperForCondition(condition) : ""}

    function addPauseData({ frames, scopes, objects }) {
      finalData.frames.push(...(frames || []));
      finalData.scopes.push(...(scopes || []));
      finalData.objects.push(...(objects || []));
    }

    function getTopFrame() {
      const { frame, data } = sendCommand("Pause.getTopFrame");
      addPauseData(data);
      return finalData.frames.find((f) => f.frameId == frame);
    }

    const bindings = [{ name: "displayName", value: functionName || "" }];
    const { result } = sendCommand("Pause.evaluateInFrame", {
      frameId,
      bindings,
      expression: "[" + "${escapedCode}" + "]",
      useOriginalScopes: true,
    });
    const values = [];
    addPauseData(result.data);
    if (result.exception) {
      values.push(result.exception);
    } else {
      {
        const { object } = result.returned;
        const { result: lengthResult } = sendCommand("Pause.getObjectProperty", {
          object,
          name: "length",
        });
        addPauseData(lengthResult.data);
        const length = lengthResult.returned.value;
        for (let i = 0; i < length; i++) {
          const { result: elementResult } = sendCommand("Pause.getObjectProperty", {
            object,
            name: i.toString(),
          });
          values.push(elementResult.returned);
          addPauseData(elementResult.data);
        }
      }
    }
    return [
      {
        key: point,
        value: { time, pauseId, point, location, values, data: finalData },
      },
    ];
  `;
}